.hr-text{
    line-height:1em;
    margin: 1rem 5%;
}
.label h1{
    text-transform: uppercase;
}
.sol::after{
    -webkit-animation:solution 6s linear infinite alternate;
            animation:solution 6s linear infinite alternate;
    content:"";
}
.label .apps::after{
    -webkit-animation:apps 6s linear infinite alternate;
            animation:apps 6s linear infinite alternate;
    content:"";
}

.herocontainer{
    overflow:hidden;
    background-color:#000b76 ;
    color:  var(--pageColor);
}
.herocontainer svg path{
    -webkit-animation:pathAnim 7s;
            animation:pathAnim 7s; 
	-webkit-animation-timing-function: linear; 
	        animation-timing-function: linear;
	-webkit-animation-iteration-count: infinite;
	        animation-iteration-count: infinite;
}
.herocontainer svg{
    display:block;
}


.herocontainer .intro{
    font-size: clamp(2.5rem,6vw,4rem);
    text-transform: uppercase;
    text-align: center;

}
.herocontainer .intro div{
    -webkit-animation: 5s changecolor infinite ease-in-out;
            animation: 5s changecolor infinite ease-in-out;
    display: inline;
}
h1 div,h2 div{
    display: inline;
}
.hero{
    padding:2.5rem 5% 0 5%;
}

@media (min-width:720px){
    .intro{
        margin-top: 2rem;
    }
    .herocontainer .label{
        padding-top: 5rem;
    }
}
