.paper-plane-animation{
    background-image: linear-gradient(to top, #fdcbf1 0%, #fdcbf1 1%, #e6dee9 100%);
    position: relative;
    overflow: hidden;
    margin-bottom: 2000px;
}
body{
    overflow-x: hidden;
}

.paper-plane{
    height: clamp(50px,15vw,70px);
    position: absolute;
    opacity: 1;
}

